.Quote-Text-Value {
    font-family: Arial, serif;
}

.Quote-Container {
    width: 90%;
    margin: auto;
    background-color: unset;
}

.Quote-Header-Container {
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    .Quote-Header-Container {
        flex-direction: column;
    }
}

.Quote-Header-Logo {
    flex-basis: 80%;
    text-align: left
}

.Quote-Spec-Container {
    margin-top: 0.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Quote-For-Container {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
}

.Quote-For-Inner-Container {
    display: flex;
    flex-direction: row;
}

.Quote-Info-Container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 1rem;
}

.Quote-Underline {
    text-decoration: underline;
    padding-bottom: 1.2rem;
}

.Quote-Label {
    font-size: 20px;
}

.Grid-Form-Container {
    display: flex;
    justify-content: space-between;
    padding-top: 1rem;
}

.Quote-Entreprise-Spec {
    flex-basis: 30%;
    align-self: center;
}

.Quote-Text {
    text-align: left;
}

.Quote-Text-Field {
    border-radius: 5px;
    background-color: rgba(195, 183, 79, 0.2);
    border: 1px solid black;
    font-size: 17px;
}

.Quote-Text-Area-Label {
    vertical-align: center;
    white-space: nowrap;
    padding-right: 0.5rem;
    text-align: left;
}

.Quote-Text-Field--Area {
    resize: vertical;
}
.Quote-Separator {
    margin-top: 2rem;
}

.Quote-Separator--Bottom {
    margin-bottom: 2rem;
}

.Quote-Text-Field--Area--extra {
    resize: none;
    width: 99%;
}

.Quote-Grid-Text-Area {
    resize: none;
    width: 98%;

}

.Quote-Grid-Number-Field {
    resize: none;
    width: 80%;
}

.Quote-Grid-Top-Aligned {
    vertical-align: top;
}


.Quote-Grid-Add-Button {
    width: 40%;
    border-radius: 7px;
    color: white;
    background-color:  rgba(195, 183, 79, 1);;
    font-size: 18px;
    border: none;
    padding: 0.5rem;
    margin-top: 0.5rem;
    max-height: 40px;
}

.Quote-Grid-Add-Button:hover {
    cursor: pointer;
    background-color: #7d3d15;
}

.Quote-Grid-Add-Button-Special {
    width: 20%;
}

@media only screen and (max-width: 600px) {
    .Quote-Grid-Add-Button {
        font-size: 14px;
    }
}

.Quote-Grid-Delete-Button {
    border: none;
    background-color: unset;
    overflow: clip;

    position: absolute;
    left: -20px;
    top: 12px;
}

.Quote-Grid-Delete-Container {
    position: relative;
}

.Quote-Grid-Delete-Button:hover {
    cursor: pointer;
    color:  rgba(195, 183, 79, 1);
}

.Quote-Grid-Polyfill {
    width: 102%;
    text-align: left;
}

.Quote-Generate-Button {
    padding: 2rem 2rem;
}

.Quote-Grid-Static-Headers {
    text-align: left;
    font-weight: bold;
}

.Controller-Container {
    display: flex;
    justify-content: center;
    gap: 1.5rem;

}
.Result-Container {
    width: 50%;
    page-break-inside: avoid;
}

.Quote-Grid-Footer-Layout {
    display:flex;
    justify-content: space-between;
}

.Grid-Bar-Hidden {
    display: none;
}

.Quote-Transparent-Input {
    outline: 0;
    border: 0;
    font-family: Arial, serif;
    font-size: 20px;
    font-weight: bold;
    text-decoration: underline;
    padding: 0;

    width: 100%;
    text-align: center;
}

.Quote-Transparent-Input--small {
    font-size: unset;
    text-decoration: unset;
    font-weight: unset;
    text-align: left;
}

.Quote-Grid-Table-Value {
    text-align: right;
}

.Quote-Grid-Table-Value-Input {
    width: 100%;
}

.Quote-Text-Value-Special {
    font-weight: lighter;
}

.Quote-Force-Print-Block {
    page-break-inside: avoid;
}

#quote_date {
    text-align: right;
}